<template>
<div style="  position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color:#fff;;
        z-index: 202;
        overflow: auto;" v-title data-title="门店地址">
   <div v-for='(value,index) in items' class='list'>
       <div  v-bind:style="{ 'background-image': 'url(' +value.img + ')','background-repeat':'no-repeat','background-size':'cover' }" class='left'></div>
       <div class='right'>
         <p style="font-size:15px;color:#000;">杭州店</p>
         <p>服务时间：{{value.remark}}</p>
         <p style="padding-bottom: 0">{{value.address}}</p>
       </div>
   </div>
</div>
</template> 

<script>
// import Vue from 'vue'

export default {
  name: 'index',
  data () {
    return {
      qu:'',
      details:'',
      name:'',
      phone:'',
      id:'',
      text:'保存地址',
      items:''
    }
  },
  watch:{

},
created(){
    this.city();
},
  methods:{ 
    
    city(){
         this.$http.post(AllUrl+'store/list',{},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          
                          // localStorage.myToken=JSON.parse(res.bodyText).token
                          console.log(JSON.parse(res.bodyText).store)
                          this.items=JSON.parse(res.bodyText).store;
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
    },
    btn(){
               this.$http.post(AllUrl+'userAddress/saveOrUpdateAddr',{
              'phone':this.phone,'token':localStorage.myToken,'name':this.name,'address':this.details,'area':'杭州市','addId':this.id},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          
                         
                          console.log(JSON.parse(res.bodyText))
                 
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
    }
  }

}
 

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list{
  width: 88vw;
  padding-top: 20px;
  padding-bottom:20px;
  margin: 0 auto;
  height: 40px;
  border-bottom: rgba(245,245,245,1) 1px solid ;
}
.list .left{
  width: 20vw;
  float: left;
  height: 55px; 
 
}
.list .right{
  float: left;
  margin-left:3vw;

}
.list .right p{
  font-size: 13px;
  color: #666;
  padding-bottom: 7px;
}
</style>



