<script>
// import Vue from 'vue'
 import { Toast,Indicator} from 'mint-ui';
export default {
  name: 'index',
  data () {
    return {
       toid:this.$route.query.id,
       items:'',
       show:true,
       page:1
    }
  },
  watch:{

},
created(){
  // Indicator.open({
      
  //               spinnerType: 'fading-circle'
  //               });
  // alert(localStorage.mytoken)

    this.money()
      let self =this 
      let a=0
             $(window).scroll(function() {

                                if (a == 0) {
                                   // console.log(999)
                              if ( (parseInt(self.page)-1)*10==self.items.length ) {
                            
                            
                                  var scrollTop = $(this).scrollTop();
                                   var scrollHeight = $(document).height();
                                   var windowHeight = $(this).height();
                                 var new_height = scrollTop + windowHeight + 100; //底部多少时刷新
                                 
                                 if (new_height - scrollHeight >= 0) { //判断浏览器是否滑到底部
                                       //上面的代码是判断滚动条滑到底部的代码
                                                  
                                      a++  ;   
                                        self.$http.post(AllUrl+'user/orderList',{
                                              'token':localStorage.myToken,'page':self.page,
                                            },{emulateJSON:true}).then(
                                                              function (res) {            
                                                                                  // 处理成功的结果
                                                                
                                                             console.log(JSON.parse(res.bodyText))

                   
                                                            self.items=self.items.concat(JSON.parse(res.bodyText).list)  
                                                              },function (err) {
                                                          // 处理失败的结果
                                                              console.log(err)

                                                               });
                                            
                                      
                                     
                                                 }
                                          }        
                                }
                              }); //加载
},
  methods:{ 
   money(){
   
  
        this.$http.post(AllUrl+'user/orderList',{
              'token':localStorage.myToken,'page':1,},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          
                          // this.items=JSON.parse(res.bodyText).list
                          console.log(JSON.parse(res.bodyText))
                          this.items=JSON.parse(res.bodyText).list;
                          this.page++
                            // Indicator.close();
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });




    },
    todetails(id,type){
   
      this.$router.push({path: '/ordersDetails?id='+id+'&type='+type})
    }
  }

}
 

</script>

