<template>
<div v-title data-title="商城" class='recycling'>
    <div  class='swait'>
      <img :src="imgl" alt="">
    </div>

    <div style="height: 10px;background: rgba(245,245,245,1)"></div>
    <div class='box'>
      <ul>
        <li @click='tofu(value)' v-for='(value,index) in items'>
            <div v-bind:style="{ 'background-image': 'url(' +value.img + ')','background-repeat':'no-repeat','background-size':'cover' }" class='pic'></div>
            <div class='bottom'>
              <p>{{value.mobileName}}</p>
              <p>回收均价<span>￥{{value.price}}</span></p>
            </div>
        </li>
         
      </ul>
    </div>
    <div  style="    color: #d2d6de!important;
    text-align: center;
    padding: 20px 0;"> —— 我们是有底线的 ——</div>
    <div style="height:56px;"></div>
</div>
</template>

<script>
// import Vue from 'vue'
import { Toast,Indicator} from 'mint-ui';
export default {
  name: 'index',
  data () {
    return {
      items:'',
      imgI9:'../static/images/recycling_icon.png',
      right:'../static/images/index_you@2x.png',
      imgl:'',
      page:1
    }
  },
  watch:{

},
created(){
  Indicator.open({
      
                spinnerType: 'fading-circle'
                });
  sessionStorage.footerThisClick=3;
  this.getData();
      this.$http.post(AllUrl+'mobileSale/getList',{
        'page':this.page
      },{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          
                      console.log(JSON.parse(res.bodyText).list)

                      this.items=JSON.parse(res.bodyText).list;
                      this.page++
                     Indicator.close();
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
      let self =this 
      let a=0
             $(window).scroll(function() {

                                if (a == 0) {
                                  // console.log(999)
                              if ( (parseInt(self.page)-1)*10==self.items.length ) {
                              
                            
                                  var scrollTop = $(self).scrollTop();
                                  var scrollHeight = $(document).height();
                                  var windowHeight = $(self).height();
                                  var new_height = scrollTop + windowHeight + 100; //底部多少时刷新
                                  if (new_height - scrollHeight >= 0) { //判断浏览器是否滑到底部
                                       //上面的代码是判断滚动条滑到底部的代码
                                      //alert("滑到底部了");                     
                                      a++  ;   
                                        self.$http.post(AllUrl+'mobileSale/getList',{
                                              'page':self.page
                                            },{emulateJSON:true}).then(
                                                              function (res) {            
                                                                                  // 处理成功的结果
                                                                
                                                            console.log(JSON.parse(res.bodyText).list)

                   
                                                            self.items=self.items.concat(JSON.parse(res.bodyText).list)  
                                                              },function (err) {
                                                          // 处理失败的结果
                                                              console.log(err)

                                                               });
                                            
                                      
                                     
                                                  }
                                          }        
                                }
                              }); //加载
},
  methods:{ 
    tofu(value){
     
       if(localStorage.myToken&&localStorage.myToken!=''){
            localStorage.dataRecycling =JSON.stringify(value)
            console.log(localStorage.dataRecycling)
             this.$router.push({path: '/faultGs?id='+value.id})
       }else{
        this.$router.push({path: '/enter'})
       }
    },
     getData(){
     this.$http.post(AllUrl+'mine/brandList',{
              'type':2},{emulateJSON:true}).then(
                        function (res) {     
                          this.imgl=       JSON.parse(res.bodyText).brand[0]
                              console.log(JSON.parse(res.bodyText).brand)
                       
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
     },
  }

}
  

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.recycling
{
  background: rgba(245,245,245,1);
  min-height: 100vh;
}
.swait
{
  width: 100vw;
  /* height:200px; */
  /* background: red; */
}
.swait img{
  width: 100%;
  display: block;;
}
.to
{
  height: 65px;
  background:#fff;
  margin-bottom: 10px;
}
.to .left
{
  float: left;
  margin-left: 23px;
  margin-right: 15px;

}
.to .left img{
  height: 50px;
  margin-top: 7.5px;
}
.to #r1{
  height: 16px;
  margin-right: 4vw;
  float: right;
  margin-top:24.5px;
}
.to .right
{
  float: left;
}
.to .right p:nth-child(1)
{
  padding-top: 15px;
  font-size: 14px;
  color:#333;

}
.to .right p:nth-child(2)
{
  padding-top: 10px;
  font-size: 12px;
  color:#999;

}
.box
{
  font-size: 0;
}
.box li    
{
 
  display:inline-block;
  width: 49vw;
  margin-right: 2vw;
  margin-bottom: 2vw;
  height:200px;
}
.box li:nth-child(2n){
  margin-right:0;
}
.box li .pic{
  height:145px;
}
.box li .bottom{
  background: rgba(71,206,212,1);
  height: 55px;

}
.box li .bottom p:nth-child(1){
  padding-top: 12px;
  font-size: 15px;
  color: #f5f5f5;
  text-align: center;
}
.box li .bottom p:nth-child(2){
  padding-top: 10px;
  font-size: 12px;
   color: #f5f5f5;
  text-align: center;
}
.box li .bottom p:nth-child(2) span{
  font-size: 13px;
  color: #f6ff00;
}
</style>



