
<template>

  <div class="header">

      <div @click='towx'>
        <img v-if='1==clickindex'  src="./img/index_weixiu@2x.png">
        <img  v-if='1!=clickindex' src="./img/index_weixiu_h@2x.png">
        <p>维修</p>
      </div>
      <div @click='tohs'>
        <img  v-if='2==clickindex' src="./img/index_huishou@2x.png">
        <img  v-if='2!=clickindex' src="./img/index_huishou_h@2x.png">
         <p>回收</p>
      </div>
      <div @click='tosc'>
        <img v-if='3==clickindex'  src="./img/index_shangcheng@2x.png">
        <img v-if='3!=clickindex'  src="./img/index_shangcheng_h@2x.png">
         <p>商城</p>
      </div>
      <div @click='towd'>
        <img  v-if='4==clickindex' src="./img/index_wode@2x.png">
        <img  v-if='4!=clickindex' src="./img/index_wode_h@2x.png">
         <p>我的</p>
      </div>
   
    



  </div>
 
</template>

<script>

 import Vue from 'vue';
// import Mint from 'mint-ui';
// import 'mint-ui/lib/style.css';
// Vue.use(Mint);
import animate from 'animate.css'
import { Toast,MessageBox,Indicator } from 'mint-ui';

//轮播引入


export default {
  name: 'headers',

  data () {
    return {
     clickindex:1,
    }
  },
  created(){ //模板编译之后，已经挂载，此时才会渲染页面，才能看到页面上数据的展示！！！
        this.clickindex=sessionStorage.clickindex
             
  },
      watch: {
            '$route' (to, from) {//使子页面返回来时云选以下代码
              // console.log(sessionStorage.clickindex)
          console.log(to.path)
          if (to.path=='/') {this.clickindex=1}
          if (to.path=='/recycling') {this.clickindex=2}
          if (to.path=='/goods') {this.clickindex=3}
          if (to.path=='/mine') {this.clickindex=4}
                  
            // if(sessionStorage.clickindex){
            //             this.clickindex=sessionStorage.clickindex;
                        
            //         }



              }
            },
  updated(){

  },
  methods: {
    towx(){
      console.log(66)
         this.$router.push({path: '/'})
         this.clickindex=1;
         sessionStorage.clickindex=1;
    },
    tohs(){
      this.$router.push({path: '/recycling'})
         this.clickindex=2;
         sessionStorage.clickindex=2;

    },
    tosc(){
      this.$router.push({path: '/goods'})
         this.clickindex=3;
         sessionStorage.clickindex=3;

    },
    towd(){
      if(localStorage.myToken&&localStorage.myToken!=''){
            this.$router.push({path: '/mine'})
               this.clickindex=4;
               sessionStorage.clickindex=4;
    }else{
     this.$router.push({path: '/enter'})
    }
      

    }
 // 
   },
   components:{
  
   }
}

// $('#nav-icon1,#nav-icon2,#nav-icon3,#nav-icon4').click(function(){
//     $(this).toggleClass('open');
//     alert($(this).index() )
//   });
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

.header
{
  background: #fff;

  position: fixed;
  width: 100vw;
  z-index: 8;
  bottom:0;
  font-size: 0;
 }
.header>div
{
  width:25%;
  display: inline-block;
  text-align:center;
  font-size: 12px;
  /*color:#47CED4;*/
  color:#333;
  padding-bottom: 10px;
}
.header>div img
{
  padding-top: 10px;
  padding-bottom: 4px;
  
  width:20px;
}
</style>



