<template>
<div v-title data-title="登入" v-bind:style="{ 'background-image': 'url(' +bg + ')','background-repeat':'no-repeat','background-size':'cover' }" class='login'>
  <img style="height:16px;padding: 10px; position:fixed;right:10px;top:10px" @click='toclose' :src="close">
 <div class='top'>
  <p>快乐修</p>
   <p>让售后成为你最好的服务</p>
 </div>
 <div class='box'>
    <p><img :src="imgI11"><input v-model='phone' type="tel" placeholder="请输入手机号码"></p>
    <p><img :src="imgI13"><input v-model='code' type="tel" placeholder="请输验证码">
        <button class='p1' @click="send">
                  <span v-if="sendMsgDisabled"><em>{{time}}s </em></span>
                  <span v-if="!sendMsgDisabled">发送验证码</span>
                  </button> 
   </p>
    <p><img :src="imgI12"><input v-model='password' type="password" placeholder="登入密码" ></p>
    <div @click='btn' class='btn'>确定</div>
 </div>
</div>
</template> 

<script>
// import Vue from 'vue'
import { Toast } from 'mint-ui';
import { Indicator } from 'mint-ui';
export default {
  name: 'index',
  data () {
    return {
      bg:'../static/images/Login_beijing@2x.png',
      imgI11:'../static/images/Login_shouji@2x.png',
      imgI12:'../static/images/Login_shuo@2x.png',
      imgI13:'../static/images/Login_dun@2x.png',
      right:'../static/images/index_you@2x.png',
      phone:'',
      password:'',
        time: 60, // 发送验证码倒计时
    sendMsgDisabled: false,
    tophone:'',
    forget:'',
    code:'',
    wl:this.$route.query.wl,
    close:'../static/images/Login_X@2x.png'
    }
  },
  watch:{

},
  methods:{ 
    btn(){
        if(this.wl==1){
          // alert(1)
                  this.$http.post(AllUrl+'user/forgetPwd',{
              'phone':this.phone,'password':this.password,'code':this.code},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          console.log(res)
                          if ( JSON.parse(res.bodyText).return_code==0) {
                            localStorage.myToken=JSON.parse(res.bodyText).token
                            this.$router.push({path: '/'})
                          }else{
                              Toast({
                                message: JSON.parse(res.bodyText).return_msg,
                                position: 'top'
                              });
                          }
                
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
        }else{
          // alert(2)
               this.$http.post(AllUrl+'user/register',{
              'phone':this.phone,'pwd':this.password,'code':this.code},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          console.log(res)
                          if ( JSON.parse(res.bodyText).return_code==0) {
                            localStorage.myToken=JSON.parse(res.bodyText).token
                            this.$router.push({path: '/'})
                          }else{
                              Toast({
                                message: JSON.parse(res.bodyText).return_msg,
                                position: 'top'
                              });
                          }
                
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
        }
             
    },
toclose(){
  this.$router.push({path: '/'})
},
        send() {
            
              let url 
              if (this.wl==1){
              
                url='user/forgetCode';
              }else{
               url = 'user/loginCode';
              }

              if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))){ 
                  Toast({
                    message: '手机号码不正确',
                    position: 'top'
                  });

                  return false; 
              } 

       this.$http.post(AllUrl+url,{
              'phone':this.phone},{emulateJSON:true}).then(
                        function (res) {            
                                    //alert(this.phone)          // 处理成功的结果
                          console.log(res)
                          // if (res.body.code==1) {
                            
                              
                          //     //  this.$router.go(0);
                          //     // this.$router.push({path: '/'})
                          // }else{
                          //   Toast({
                          //     message: res.body.msg,
                          //     position: 'top'
                          //   });
                          // }
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
          
            if (this.sendMsgDisabled==false) {
           

             let me = this;
             me.sendMsgDisabled = true;
             let interval = window.setInterval(function() {
               if ((me.time--) <= 0) {
                 me.time = 60;
                 me.sendMsgDisabled = false;
                 window.clearInterval(interval);
               }
             }, 1000);
           }  
         }
  }

}
 

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.p1{
  line-height:30px;
  padding: 0 4px;
  border:rgba(71,206,212,1);
  background-color: rgba(71,206,212,1);
  position: absolute;
  top:9px;
  right:0;
  color:#fff;
    font-size: 12px;

}
.login{
 position: relative;
 z-index: 106;
  height: 100vh;
}
.top p:nth-child(1)
{
  padding-top: 25vh;
  font-size: 18px;
  color:#fff;
  font-weight: 600;
  text-align: center;
}
.top p:nth-child(2)
{
  padding-top: 18px;
  font-size: 12px;
  color:#fff;
 text-align: center;
}
.box{
  width: 85vw;
  margin:auto;
  left: 0;
  right:0;
  position: absolute;
  bottom:15vh;
  background-color: #fff;
  padding-top: 55px;
}
.box p{
  line-height: 50px;
  width: 80%;
  margin: 0 auto;
  border-bottom: 0.5px solid rgba(71,206,212,1);
  position:relative;
  text-indent: 26px;
}
.box p input{
    width: 80%;
    line-height: 46px;
    font-size: 14px;

}
p img{
  height: 24px;
  position: absolute;
  left: 0;
  top:13px; 
}
.box .btn{
  width: 61vw;
  margin: 0 auto;
  text-align: center;
  background-color: rgba(71,206,212,1);
  line-height: 55px;
  margin-top:38px;
  margin-bottom: 20px;
  font-size: 18px;
  color: #fff;
}
</style>



