<template>
<div style="  position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color:#fff;;
        z-index: 202;
        overflow: auto;" v-title data-title="地址">
    <p style="border:0">用户姓名：<input type="text"  v-model='name' placeholder="输入您的名字"></p>
    <p>电话号码：<input type="tel"  maxlength="11" v-model='phone' placeholder="输入您的电话"></p>
    <p>所在地区：<span>杭州市</span></p>
    <p>详细地址：<input type="text"  v-model='details' placeholder="输入您的详细地址"></p>
    <div @click='btn' class='btn'>{{text}}</div>
</div>
</template> 

<script>
// import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'index',
  data () {
    return {
      qu:'',
      details:'',
      name:'',
      phone:'',
      id:'',
      text:'保存地址'
    }
  },
  watch:{

},
created(){
    this.city();
},
  methods:{ 
    
    city(){
         this.$http.post(AllUrl+'userAddress/list',{
              'token':localStorage.myToken},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          
                          // localStorage.myToken=JSON.parse(res.bodyText).token
                          console.log(JSON.parse(res.bodyText).list[0])
                 if (JSON.parse(res.bodyText).list[0]) {
                       
                        this.details=JSON.parse(res.bodyText).list[0].address;
                        this.name=JSON.parse(res.bodyText).list[0].name;
                        this.phone=JSON.parse(res.bodyText).list[0].phone;
                        this.id=JSON.parse(res.bodyText).list[0].id
                 }
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
    },
    btn(){
               this.$http.post(AllUrl+'userAddress/saveOrUpdateAddr',{
              'phone':this.phone,'token':localStorage.myToken,'name':this.name,'address':this.details,'area':'杭州市','addId':this.id},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          if(JSON.parse(res.bodyText).return_code==0){
                             this.$router.go(-1);
                          }else{
                              Toast({
                                message: JSON.parse(res.bodyText).return_msg,
                                position: 'top'
                              });
                          }
                         
                          // console.log(JSON.parse(res.bodyText).return_code)
                 
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
    }
  }

}
 

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.btn{
  width: 90vw;
  line-height: 50px;
  background: #47ced4;
  color: #fff;
  font-size: 14px;
  margin: 0 auto;
  margin-top: 25px;
  text-align: center;
}
p
{
  line-height: 40px;
  padding-left: 5vw;
  font-size: 13px;
  color:#666666;
  border-top: rgba(248,248,248,1) 1px solid;
}
input,span{
  font-size: 13px;
  color: #999999;
  line-height: 38px;
}
</style>



