<template>
<div v-title data-title="填写信息" 
  style="position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color:#fff;;
        z-index: 202;
        overflow: auto;"class='fault'>
   <div class='headers'>
       <div v-bind:style="{ 'background-image': 'url(' +items.img + ')','background-repeat':'no-repeat','background-size':'cover' }" class='left'></div>
      <div class='right'>
        <p>{{items.mobileName}}</p>
        <p>回收价格<span>￥{{items.price}}</span></p>
      </div>
  </div>
    <div style="height: 10px;background:rgba(245,245,245,1)"></div>                    

 <div style="height: 10px;background:rgba(245,245,245,1)"></div>
  <div class='top'>
   <p @click='city'>{{details}}  <img id='r1' :src="right"></p>
   <p>联系电话<input v-model="phone" style="padding-left:13px;" type='tel' placeholder="联系人手机好" ></p>
   <p>短信验证 <input v-model="code" style="padding-left:13px;" type='tel' placeholder="请输入验证码" > 

    <button class='p1' @click="send">
                  <span v-if="sendMsgDisabled"><em>{{time}}s </em></span>
                  <span v-if="!sendMsgDisabled">发送验证码</span>
                  </button> 

  </p>
<textarea v-model="remark" id="zw" type="text" maxlength="200"  placeholder="再补充描述下设备的问题（选填）"></textarea> 
   
 </div>
  <div style="height: 10px;background:rgba(245,245,245,1)"></div> 
    <div class='bottom'>
      <p @click='toshow'>上门维修<img  v-show='show' :src='imgI9' ></p>
      <p @click='toshow'>到店维修<img  v-show='!show' :src='imgI9'></p>
    </div> 
    <div @click='btn' class='btn'>确定下单</div>
</div>
<!-- </div> -->
</template>

<script>
// import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'index',
  data () {
    return {
    toid:this.$route.query.id,
    items:'',
    contents:'',
        time: 60, // 发送验证码倒计时
    sendMsgDisabled: false,
    phone:'',
    forget:'',
    code:'',
    imgI9:'../static/images/icon_dui@2x.png',
    show:true,
    remark:'',
    right:'../static/images/index_you@2x.png',
    details:'回收地址',
    mobileId:this.$route.query.id,
    addressId:''
    }
  },
  watch:{

},
created(){
console.log(this.items)
    this.items=JSON.parse(localStorage.dataRecycling)
    

    this.$http.post(AllUrl+'userAddress/list',{
              'token':localStorage.myToken},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          console.log(JSON.parse(res.bodyText))
                     
                 if (JSON.parse(res.bodyText).list[0]) {
                       
                        this.details=JSON.parse(res.bodyText).list[0].address;
                        this.addressId=JSON.parse(res.bodyText).list[0].id
                 }
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
},
  methods:{ 
    btn(){
      if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))){ 
                  Toast({
                    message: '手机号码不正确',
                    position: 'top'
                  });

                  return false; 
              } 
              let statu
              if(this.show){
                statu=0;
              }else{
                statu=1;
              }
        this.$http.post(AllUrl+'mobileRecovery/createOrder',{
            'token':localStorage.myToken,'mobileId':this.mobileId,'addressId':this.addressId,'status':statu,'remark':this.remark,'phone':this.phone,'code':this.code},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          console.log(res)
                          if ( JSON.parse(res.bodyText).return_code==0) {
                            Toast({
                                message: '提交成功',
                                position: 'top'
                              });
                           this.$router.push({path: '/orders'})
                          }else{
                              Toast({
                                message: JSON.parse(res.bodyText).return_msg,
                                position: 'top'
                              });
                          }
                
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
             
    },
    toshow(){
        this.show=!this.show;
        
    },
    city(){
      this.$router.push({path: '/city'})
    },
        send() {
     
              if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))){ 
                  Toast({
                    message: '手机号码不正确',
                    position: 'top'
                  });

                  return false; 
              } 

       this.$http.post(AllUrl+'mine/getCode',{
              'phone':this.phone},{emulateJSON:true}).then(
                        function (res) {            
                                 //   alert(this.phone)          // 处理成功的结果
                          console.log(res)
                    
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
          
            if (this.sendMsgDisabled==false) {
           

             let me = this;
             me.sendMsgDisabled = true;
             let interval = window.setInterval(function() {
               if ((me.time--) <= 0) {
                 me.time = 60;
                 me.sendMsgDisabled = false;
                 window.clearInterval(interval);
               }
             }, 1000);
           }  
         },

  }

}
 

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#r1{
  height: 16px;
  margin-right: 4vw;
  float: right;
  margin-top:17.5px;
}
.fault{
  background: rgba(245,245,245,1);
  height:100vh;
  position: relative;
  z-index: 9;
}
input{
  color: #999;
  font-size: 12px;
  padding-left: 13px;
  line-height: 46px;
}
#zw{
  width: 90vw;
  display: block;
  padding: 0 5vw;
  border:0;
  height: 60px;
  padding-top: 10px;
  font-size: 12px;
  color: #999;
  border-top: rgba(248,248,248,1) 1px solid;
}
.headers{
  height: 80px;
  width: 100vw;
  background: #fff;
}
.headers .left{
  height:65px;
   width: 100px;
   float: left;
   margin-top: 7.5px;
   margin-left: 5vw;
   background: red;
}
.headers .right{
  float: left;
  margin-left: 32px;
}
.headers .right p:nth-child(1){
  font-size: 18px;
  margin-top: 16px;
  margin-bottom: 18px;
  color: #666;
}
.headers .right p:nth-child(2){
  font-size: 14px;
  color: #999;
}
.headers .right p:nth-child(2) span{
  font-size: 14px;
  color: #47ced4;
}
.recycling
{
  background: #999;
}
.top
{
  /*margin-bottom: 10px;*/
  background: #fff;
}
.top p,.bottom p
{
  background: #fff;
  line-height: 50px;
  padding-left: 5vw;
  font-size: 12px;
  color:#666;
  border-top: rgba(248,248,248,1) 1px solid;
}
p img{
  float: right;
  height: 16.5px;
  margin-top:17px;
  margin-right: 5vw;
}
.top p .p1{
  border: 0;
  float: right;
  background:#47ced4;
  font-size: 12px;
 margin-right: 5vw;
  color: #fff;
   padding:0 5px;
  line-height: 30px;
  margin-top: 10px;
  text-align: center;
}
.btn{
  margin: 0 auto;
  width: 80vw;
  margin-top: 10px;
  font-size: 18px;
  color: #f5f5f5;
  line-height: 54px;
  text-align: center;
  background: #47ced4;
}
</style>


