<template>
<div v-title data-title="选择颜色" style="position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color:#fff;;
        z-index: 202;
        overflow: auto;">
  <div  class='box'>
    <ul>
      <li @click='fault(value.id,value.name)' v-for='(value,index) in items'>{{value.name}}</li>
    </ul>
  </div>
    <transition name="router-slid" mode="out-in">
            <router-view></router-view>
        </transition>
</div>
</template> 

<script>
// import Vue from 'vue'

export default {
  name: 'index',
  data () {
    return {
     data:'下一步',
     id:this.$route.query.id,
     thisclick:0,
     items:''
    }
  },
  watch:{

},
 created () {
  this.tolist()
  
  localStorage.mobileName=this.id;
 },
  methods:{ 
 
    tolist(){
   
      // alert(id)
      // this.$router.push({path: '/tojx?id='+id})

        this.$http.post(AllUrl+'mobile/getListByMobile',{
              'mobileId':this.id},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          
                          this.items=JSON.parse(res.bodyText).list
                          console.log(JSON.parse(res.bodyText))
               
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });




    },
     fault(id,name){
      localStorage.wcolorName=localStorage.wcolorName+name;
      localStorage.colormobileId=id
      this.$router.push({path: '/tother'})
     
    }
  }

}
 

</script>

