<template>
<div v-title data-title="选择品牌" style="  position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color:#fff;;
        z-index: 202;
        overflow: auto;">
  <div  class='box'>
    <div style='height:10px;'></div>
    <h1>手机品牌</h1>
    <p class='p1'>
      <span class='span1' v-bind:class='{span2:index==thisclick}'  @click='tolist(index,value.id)' vid:='value.id' v-for='(value,index) in data'>{{value.name}}</span>
     </p>
    <ul>
      <li @click='tocolor(value.id)' v-for='(value,index) in items'>{{value.name}}</li>
    </ul>
  </div>
      <transition name="router-slid" mode="out-in">
            <router-view></router-view>
        </transition>
</div>
</template> 

<script>
// import Vue from 'vue'

export default {
  name: 'index',
  data () {
    return {
     data:'下一步',
     id:this.$route.query.id,
     thisclick:0,
     items:'',
     tojxhsMid:''
    }
  },
  watch:{

},
 created () {
  this.to()
  
    // localStorage.mobileMemory=this.id;
 },
  methods:{ 
         to(){
          // let id = parseInt(this.id)+1
          let status=0;
        if (this.id==1) {
          status=1;
        }
               this.$http.post(AllUrl+'mobile/getList',{
              'status':status},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          
                          this.data=JSON.parse(res.bodyText).list
                          console.log(this.data )
                            this.tolist(0,this.data[0].id)

                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
    },
    tolist(index,id){
      this.thisclick=index
      // alert(id)
      // this.$router.push({path: '/tojx?id='+id})
          // localStorage.tojxhsMid=id;
          this.tojxhsMid=id
        this.$http.post(AllUrl+'mobile/getListByMobile',{
              'mobileId':id},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          
                          this.items=JSON.parse(res.bodyText).list
                          console.log(JSON.parse(res.bodyText))
               
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });




    },
    tocolor(tid){
     
    localStorage.mobileName=tid;
    if (this.id==1) {
      this.$router.push({path: '/tofaultHs?id='+tid+'&tojxhsMid='+this.tojxhsMid})
    }else{
      this.$router.push({path: '/tocolor?id='+tid})
    }

      
    }
  }

}
 

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.box{
  padding: 0 5vw;
  margin: 0 auto;
}
ul{
padding-top: 20px;
font-size: 0;
}
li{
  width: 43vw;
  display: inline-block;
  line-height: 40px;
  text-align: center;
  border:rgba(71,206,212,1) 1px solid;
  margin-bottom: 10px;
  font-size: 12px;
  color: #999;
}
li:nth-child(2n){
  margin-left:2vw;
}
h1{
  border-left:6px solid rgba(71,206,212,1);
  font-size: 15px;
  color: #666;
  line-height: 18px;
  text-indent: 5px;
  margin-bottom: 25px;
}
.p1 {
font-size: 0;
}
.span1{
  display: inline-block;
  font-size: 13px;

  color: #999;
  padding-right: 30px;
}
.span2{
    color: #47ced4;
}
</style>



