<template>
<div v-title data-title="登入" style="position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color:#fff;;
        z-index: 202;
        overflow: auto;" v-bind:style="{ 'background-image': 'url(' +bg + ')','background-repeat':'no-repeat','background-size':'cover' }" class='login'>
  <img style="height:16px;padding: 10px; position:fixed;right:10px;top:10px" @click='toclose' :src="close">
 <div class='top'>
  <p>快乐修</p>
   <p>让售后成为你最好的服务</p>
 </div>
 <div class='box'>
    <p><img :src="imgI11"><input v-model='phone' type="tel" maxlength="11" placeholder="请输入手机号码"></p>
    <p><img :src="imgI12"><input v-model='password' type="password" placeholder="登入密码" ></p>
    <div class='wl' @click='wl'>忘记密码?</div>
    
    <div @click='btn' class='btn'>确定</div>
<div class='new' @click='news'>创建新账号</div>
    
 </div>
</div>
</template> 

<script>
// import Vue from 'vue'
import { Toast } from 'mint-ui';
export default {
  name: 'index',
  data () {
    return {
      bg:'../static/images/Login_beijing@2x.png',
      imgI11:'../static/images/Login_shouji@2x.png',
      imgI12:'../static/images/Login_shuo@2x.png',
      right:'../static/images/index_you@2x.png',
      phone:'',
      password:'',
      close:'../static/images/Login_X@2x.png'
    }
  },
  watch:{

},

  methods:{ 
    toclose(){
  this.$router.push({path: '/'})
},
    btn(){
               this.$http.post(AllUrl+'user/login',{
              'phone':this.phone,'password':this.password},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          
                            if ( JSON.parse(res.bodyText).return_code==0) {
                              console.log(JSON.parse(res.bodyText))
                             
                            localStorage.myToken=JSON.parse(res.bodyText).token
                           localStorage.myavimg=JSON.parse(res.bodyText).people.img
                         localStorage.myname=JSON.parse(res.bodyText).people.nickName
                            this.$router.push({path: '/mine'})
                          }else{
                              Toast({
                                message: JSON.parse(res.bodyText).return_msg,
                                position: 'top'
                              });
                          }
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });
    },
    wl(){
      this.$router.push({path: '/login?wl=1'})
    },
    news(){
      this.$router.push({path: '/login'})
    }
  }

}
 

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
 position: relative;
 z-index: 99999;
  height: 100vh;
}
.wl{
  float: right;
  font-size: 12px;
  color:#666;
  margin-right: 10%;
  padding-top: 10px;
}
.top p:nth-child(1)
{
  padding-top: 25vh;
  font-size: 18px;
  color:#fff;
  font-weight: 600;
  text-align: center;
}
.top p:nth-child(2)
{
  padding-top: 18px;
  font-size: 12px;
  color:#fff;
 text-align: center;
}
.box{
  width: 85vw;
  margin:auto;
  left: 0;
  right:0;
  position: absolute;
  bottom:8rem;
  background-color: #fff;
  padding-top: 1rem;
}
.box p{
  line-height: 50px;
  width: 80%;
  margin: 0 auto;
  border-bottom: 0.5px solid rgba(71,206,212,1);
  position:relative;
  text-indent: 26px;
}
.box p input{
    width: 80%;
    line-height: 46px;
    font-size: 14px;
}
p img{
  height: 24px;
  position: absolute;
  left: 0;
  top:13px; 
}
.box .btn{
  width: 61vw;
  margin: 0 auto;
  text-align: center;
  background-color: rgba(71,206,212,1);
  line-height: 50px;
  margin-top:38px;

  font-size: 18px;
  color: #fff;
}
.new{
  line-height: 50px;
    font-size: 12px;
  color:#666;
  text-align: center;
}
</style>



