<template>

<div calss='fault'>
        <div v-if='0==this.type'>
          <div class='top'>
            <p style="border:0;color: #47ced4;">维修订单<span>￥{{items.price}}</span></p>
          <p >维修方案：{{items.result}} </p>
          <p>维修手机：{{items.mobile}}</p>
          <p>故障：{{items.question}}</p>
          <p style="color:#47ced4">注：以上问题以工程师为主</p>
        </div>
    </div>
  <div  v-if='1==this.type'>
    <div style="font-size:14px;padding-left:5vw;line-height:30px;border-bottom:rgba(248,248,248,1) 1px solid;; color: #47ced4;">回收订单</div>
      <div class='headers'>
        <div v-bind:style="{ 'background-image': 'url(' +items.img + ')','background-repeat':'no-repeat','background-size':'cover' }" class='left'></div>
        <div class='right'>
          <p>{{items.mobile}}</p>
          <p>回收价格<span>￥{{items.price}}</span></p>
        </div>
    </div>
  </div>
 <div  v-if='2==this.type'>
   <div style="font-size:14px;padding-left:5vw;line-height:30px;border-bottom:rgba(248,248,248,1) 1px solid;; color: #47ced4;">商品订单</div>
      <div class='headers'>
        <div v-bind:style="{ 'background-image': 'url(' +items.img + ')','background-repeat':'no-repeat','background-size':'cover' }" class='left'></div>
        <div class='right'>
          <p>{{items.mobile}}</p>
          <p>回收价格<span>￥{{items.price}}</span></p>
        </div>
    </div>
  </div>
    <div style="height: 10px;background:rgba(245,245,245,1)"></div>

    <div class='top bottom'>
   <p>{{city}}<span>{{name}}</span></p>
   <p>联系电话<span>{{items.phone}}</span></p>
   <p class='p3' style="line-height:60px;">备注：<span class='span2'>{{items.remark}}</span></p>
    <div style="height: 10px;background:rgba(245,245,245,1)"></div>
   <p>维修方式<span>{{items.orderStatus}}</span></p>
 </div>


  </div> 

</template>

<script>
// import Vue from 'vue'

export default {
  name: 'index',
  data () {
    return {
       toid:this.$route.query.id,
        type:this.$route.query.type,
       items:'',
       show:true,
       city:'',
       name:'',
       phone:'',
       remark:''
    }
  },
  watch:{

},
created(){
    this.$http.post(AllUrl+'userAddress/list',{
              'token':localStorage.myToken},{emulateJSON:true}).then(
                        function (res) {            
                                            // 处理成功的结果
                          console.log(JSON.parse(res.bodyText))
                     console.log(res)
                 if (JSON.parse(res.bodyText).list[0]) {
                       
                        this.city=JSON.parse(res.bodyText).list[0].address;
                        this.name=JSON.parse(res.bodyText).list[0].name;
                        this.phone=JSON.parse(res.bodyText).list[0].phone;

                 }
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });


    this.money()
    
},
  methods:{ 
   money(){
   
  
        this.$http.post(AllUrl+'user/orderDetail',{
              'token':localStorage.myToken,'orderId':this.toid,'type':this.type},{emulateJSON:true}).then(
                        function (res) {            
                                              // 处理成功的结果
                          
                          // this.items=JSON.parse(res.bodyText).list
                          console.log(res)
                          // console.log(JSON.parse(res.bodyText))
                          this.items=JSON.parse(res.bodyText).data;
               
                        },function (err) {
                    // 处理失败的结果
                        console.log(err)

                        });




    },
  }

}
 

</script>

