import index from '@/components/index/index'
import recycling from '@/components/index/recycling'
import goods from '@/components/index/goods'
import fault from '@/components/index/fault'
import mine from '@/components/index/mine'
import login from '@/components/index/login'
import enter from '@/components/index/enter'
import tother from '@/components/index/tother'
import tojx from '@/components/index/tojx'

import tojxhs from '@/components/index/tojxhs'
import tofaultHs from '@/components/index/tofaultHs'

import tocolor from '@/components/index/tocolor'
import city from '@/components/index/city'
import mdCity from '@/components/index/mdCity'
import faultHs from '@/components/index/faultHs'
import faultGs from '@/components/index/faultGs'
import orders from '@/components/index/orders'
import ordersDetails from '@/components/index/ordersDetails'

Vue.use(Router)

export default new Router({
    mode:'history',
  routes: [
   { path: '*', component: index },
    {
      path: '/',
      name: 'index',
      component: index,
      meta: { keepAlive: true },
      children:[
        {
            path: '/enter',
            name: 'enter',
            component: enter
          },
          {
          path: '/tojx',
          name: 'tojx',
          component: tojx,

          children:[
               {
                path: '/tocolor',
                name: 'tocolor',
                component: tocolor,
                 children:[
                       {
                        path: '/tother',
                        name: 'tother',
                        component: tother,
                        children:[
                              {
                              path: '/fault',
                              name: 'fault',
                              component: fault
                            },
                        ]
                      },

                 ]
              },

          ]
        },
             {
            path: '/cityindex',
            name: 'cityindex',
            component: mdCity
          },
         
      ]
    },
      {
      path: '/recycling',
      name: 'recycling',
      component: recycling,
       meta: { keepAlive: true },
      children:[
          {
            path: '/tojxhs',
            name: 'tojxhs',
            component: tojxhs,
            children:[
                {
                  path: '/tofaultHs',
                  name: 'tofaultHs',
                  component: faultHs
                },
                 {
                  path: '/enter',
                  name: 'enter3',
                  component: enter
                },
            ]
          },
          {
            path: '/faultHs',
            name: 'faultHs',
            component: faultHs
          },
      ]
    },
  
    {
      path: '/faultGs',
      name: 'faultGs',
      component: faultGs
    },
  
    {
      path: '/ordersDetails',
      name: 'ordersDetails',
      component: ordersDetails
    },
    
      {
      path: '/goods',
      name: 'goods',
      component: goods,
       meta: { keepAlive: true },
    },
 
     {
      path: '/mine',
      name: 'mine',
      component: mine,
       // meta: { keepAlive: true },
      children:[
           {
          path: '/orders',
          name: 'orders',
          component: orders
           },   
            {
            path: '/mdCity',
            name: 'mdCity',
            component: mdCity
          }, 
             {
            path: '/city',
            name: 'city',
            component: city
          },
           {
          path: '/enter',
          name: 'enter1',
          component: enter
        },
      ]
    },
   
     {
      path: '/login',
      name: 'login',
      component: login
    },
 
   
 
    //  {
    //   path: '/city',
    //   name: 'city',
    //   component: city
    // },
  
    
  ]
})




